/*------------------------------------*\
    部落格文章列表樣式 */
/*------------------------------------*\ */

.blog-list {

    &-item {
        display: flex;
        margin-bottom: 60px;
        &:last-child {
            margin-bottom: 124px; } }

    &-img {
        @include size(50%,400px);
        margin-right: 24px; }

    &-content {
        @include flexGrid(space-between,flex-end);
        flex-direction: column;
        width: 50%; }

    &-title {
        @include fontSize(32px,48px);
        color: $color-third; }

    &-info {
        @include flexGrid(space-between);
        margin-bottom: 16px; }

    &-subtitle {
        @include fontSize(20px,30px); }

    &-date {
        @include fontSize(16px,24px); }

    &-word {
        @include fontSize(20px,30px);
        color: #555555; }

    &-link {
        @include fontSize(36px,54px,bold);
        color: $color-third;
        transition: .3s;

        &:hover {
            color: $color-primary; } } }
