/*------------------------------------*\
    部落格內文設定 */
/*------------------------------------*\ */

.blog {

    &-article {
        display: flex;

        &-content {
            width: 70%;
            margin-right: 24px; }

        &-tag {
            margin-bottom: 8px;
            display: flex;

            &Word {
                @include fontSize(24px,36px,bold);
                color: $color-primary;
                margin-right: 15px; } }

        &-title {
            @include fontSize(48px,72px); }

        &-subtitle {
            @include fontSize(24px,36px);
            margin-bottom: 16px; }

        &-img {
            @include size(100%,520px);
            object-fit: cover;
            margin-bottom: 8px; }

        &-word {
            @include fontSize(16px,24px);
            color: #555555;
            margin-bottom: 40px; } }

    &-share {
        @include flexGrid(flex-start);
        padding-bottom: 16px;
        margin-bottom: 24px;
        border-bottom: 1px solid $color-black;

        &-word {
            margin-right: 16px; }

        &-list {
            display: flex; }

        &-icon {
            @include size(24px);
            margin-right: 16px; } }

    &-page {

        &-btns {
            @include flexGrid(space-between);
            width: 100%; }

        &-prev {
            transform: rotate(180deg); }

        &-link {
            @include flexGrid;
            color: $color-black;

            >.blog-page-arrow {
                margin-right: 16px;
                margin-left: 0; }

            &.reverse {
                flex-direction: row-reverse;

                >.blog-page-arrow {
                    margin-right: 0;
                    margin-left: 16px; } } }

        &-arrow {
            @include size(16px); } }

    &-tag {

        &-menu {
            width: 28%; }

        &-icon {
            margin-right: 12px; }

        &-title {
            @include flexGrid(flex-start);
            margin-bottom: 4px; }

        &-word {
            @include fontSize(32px,48px,bold);
            color: $color-primary; }

        &-item {
            @include fontSize(24px,36px,bold);
            color: $color-primary;
            display: inline-block;
            margin: 0 24px 4px 0; } } }
